.help-card{
    /* display: flex;
    flex-direction: column;
    height: 93.3vh;
    width: 100%; */
    height: 100vh;
    width: 100%;
    background-color: black;
    position: relative;
    opacity: 0.9999;
    color: white;
    padding-left: 20px;
    /* overflow-x: hidden; */
    /* justify-content: center;
    align-items: center;
    color: white;
    text-align: center; */
  }
.help-card::before {
    content: "";
    background: url("./nationalhelp.svg") no-repeat center center/cover;
    position: absolute;
    opacity: 0.2;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .card-title{
      font-size: 35px;
  }
  .bg-warning1{
      font-size: 20px;
      background: rgb(121, 121, 43);
  }
  .bg-warning1:hover{
      background: tomato;
  }
  @media screen  and (max-width:500px){
.card-title{
    font-size: 25px;
}      
.bg-warning1{
    font-size: 15px;
}
.help-card{
    padding-left: 5px;

}
  }